<template>
  <div class="app-content">
    <tax-header min>
      <div class="register"><i class="iconfont icon-person"></i>
        <swak-link to="/register">立即注册</swak-link>
      </div>
    </tax-header>
    <div class="login-bar">
      <div class="-inner">
        <div class="-login">
          <a class="-image" @click.stop="doMock"><img :src="this.qrcode"/></a>
          <div class="-desc">微信扫一扫登录</div>
        </div>
      </div>
    </div>
    <div class="main">
      <div class="title">帐号分类</div>
      <div class="content account-types">
        <div class="account-type depart">
          <div class="-inner">
            <div class="-icon"><i class="iconfont icon-department"></i></div>
            <div class="-text">给单位提供各类个税报表计税、税后收入还原、工资筹划、纳税申报等服务.并提供免费的本地管理客户端。</div>
            <div class="-btn"><a class="el-button el-button--success">了解更多</a></div>
          </div>
        </div>
        <div class="account-type person">
          <div class="-inner">
            <div class="-icon"><i class="iconfont icon-person"></i></div>
            <div class="-text">给个人提供汇算清缴、随手记账等特色服务</div>
            <div class="-btn"><a class="el-button el-button--success">了解更多</a></div>
          </div>
        </div>
      </div>
    </div>
    <tax-floot/>
  </div>
</template>

<script>
  import SwakLink from "../ui/components/link/link";
  import LoginApi from "@/api/user/login";
  import TaxHeader from "../ui/components/header/header";
  import TaxFloot from "../ui/components/floot/floot";

  export default {
    components: {
      TaxFloot,
      TaxHeader,
      SwakLink},
    name: "login",
    data() {
      return {
        token: '',
        qrcode: '',
        timer: null,
        to: '/'
      };
    },
    mounted() {
      if (this.timer != null) {
        clearTimeout(this.timer);
      }
      var _to = this.$route.query.to
      this.to  = !!_to ? _to: '/'
      this.loadQrcode();
    },
    methods: {
      loadQrcode: function () {
        LoginApi.qrcode().then(res => {
          this.token = res.obj.token;
          this.qrcode = res.obj.qrcode;
          this.touchTask();
        });
      },
      touchTask: function () {
        if (this.timer != null) {
          clearTimeout(this.timer);
        }
        this.timer = setTimeout(() => {
          this.doTouch();
        }, 1000);
      },
      doTouch: function () {
        LoginApi.touch(this.token).then(res => {
          if (res.success) {
            this.doLogin();
          } else {
            this.touchTask();
          }
        })
      },
      doLogin: function () {
        if (this.timer != null) {
          clearTimeout(this.timer);
        }
        this.$store.dispatch('Login', this.token).then(res => {
          this.$router.replace(this.to)
        })
      },
      doMock: function () {
        LoginApi.doMock(this.token).then(res => {})
      }
    },
    destroyed: function () {
      if (this.timer != null) {
        clearTimeout(this.timer);
      }
    }
  };
</script>

